import { format } from 'date-fns';
import { useWeb3Context } from '@/context/Web3Context';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardFooter } from '@/components/ui/card';
import { ThumbUpIcon, ThumbDownIcon } from '@heroicons/react/outline';


interface PostProps {
  id: number;
  message: string;
  author: string;
  likes: number;
  dislikes: number;
  createdAt: number;
  modifiedAt: number;
}

export function Post({
  id,
  message,
  author,
  likes,
  dislikes,
  createdAt,
  modifiedAt,
}: PostProps) {
  const {
    account,
    likePost,
    dislikePost,
    removeLike,
    removeDislike,
    modifyPost,
  } = useWeb3Context();

  const isAuthor = account?.toLowerCase() === author.toLowerCase();

  const handleModify = async () => {
    const newMessage = prompt('Edit your post:', message);
    if (newMessage && newMessage !== message) {
      await modifyPost(id, newMessage);
    }
  };

  return (
    <Card className="mb-4">
      <CardContent className="pt-6">
        <div className="flex justify-between items-start mb-4">
          <div>
            <p className="font-medium text-gray-900">{message}</p>
            <p className="text-sm text-gray-500">
              By: {author.slice(0, 6)}...{author.slice(-4)}
            </p>
          </div>
          {isAuthor && (
            <Button variant="outline" size="sm" onClick={handleModify}>
              Edit
            </Button>
          )}
        </div>
        <div className="text-sm text-gray-500">
          <p>Created: {format(createdAt * 1000, 'PPp')}</p>
          {modifiedAt > 0 && (
            <p>Modified: {format(modifiedAt * 1000, 'PPp')}</p>
          )}
        </div>
      </CardContent>
      <CardFooter className="flex gap-4">
        <div className="flex items-center gap-2">
          <Button
            variant="outline"
            size="sm"
            onClick={() => likePost(id)}
            className="flex items-center gap-1"
          >
            <ThumbUpIcon className="h-4 w-4" />
            {likes}
          </Button>
          <Button
            variant="outline"
            size="sm"
            onClick={() => removeLike(id)}
          >
            Remove
          </Button>
        </div>
        <div className="flex items-center gap-2">
          <Button
            variant="outline"
            size="sm"
            onClick={() => dislikePost(id)}
            className="flex items-center gap-1"
          >
            <ThumbDownIcon className="h-4 w-4" />
            {dislikes}
          </Button>
          <Button
            variant="outline"
            size="sm"
            onClick={() => removeDislike(id)}
          >
            Remove
          </Button>
        </div>
      </CardFooter>
    </Card>
  );
}